.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color:white;

}
.header_logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}
.header_center{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgray;
}
.header_icon{
display: flex;
align-items: center;

}
.header__icons{
    margin-right: 8px;
}
.header_centerButton{
    width: 50px;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}
.header_center > input{
    flex: 1;
    border: none;
}