*{
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app__page{
  display: flex;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color:white;

}
.header_logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}
.header_center{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgray;
}
.header_icon{
display: flex;
align-items: center;

}
.header__icons{
    margin-right: 8px;
}
.header_centerButton{
    width: 50px;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}
.header_center > input{
    flex: 1 1;
    border: none;
}
.recommendedVideos{
    flex: 0.8 1;
    background-color: #f9f9f9;
    padding: 40px 20px;
    padding-bottom: 0;
}
.recommendedVideos > h2{
    margin-left: 5px;
    margin-bottom: 20px;
}
.recommendedVideos__videos{
    display: flex;
    flex-wrap: wrap;

}
.videoCard{
    width: 270px;
    margin-bottom: 40px;
}
.videoCard__thumbnail{
    height: 140px;
    width: 250px;
    
}
.videoCard__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}
.videoCard__text{
    margin-left: 15px;
}
.videoCard__text > h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.videoCard__text > p{
    font-size: 14px;
    color: gray;
}
.videoCard__avatar{
    height: 30px !important;
    width: 30px !important;
}
.sidebar{
    flex: 0.2 1;
}
.sidebar > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;


}
.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
}
.sidebarRow__icon{
    color: #606060;
}
.sidebarRow__title{
    flex: 1 1;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;

}
.sidebarRow:hover{
    background-color: lightgray;
    cursor: pointrer;
}
.sidebarRow:hover>.sidebarRow__icon{
    color: red;
} 
 .sidebarRow:hover >.sidebarRow__title{
    font-weight: bold;
}
.sidebarRow.selected{
    background-color: lightgray;
}
 .sidebarRow.selected >.sidebarRow__icon{ 
    color: red;
}
.sidebarRow.selected >.sidebarRow__title{
    font-weight: bold;
} 
.searchpage{
    flex: 0.8 1;
    background-color: #f9f9f9;
    padding: 20px 20px;
}
.searchpage__filter{
    display: flex;
    align-items: center;
    color: #606060;
    font-size: xx-small !important;
}
.searchpage__filter >h2{
    margin-left: 10px;
}
.searchpage > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;

}
.channelRow{
    display: flex;
    align-items: center;
    width: 70%;
}
.channelRow__text{
    display: flex;
    flex-direction: column;
}
.channelRow__text > p{
    color: #606060;
    font-size: small !important;
}
.channelRow__logo{
    height: 120px;
    width: 120px;
    margin: 10px 60px;
}
.channelRow__text > h4{
    display: flex;
}
.videoRow{
    display: flex;
    margin-bottom: 30px;
    max-width: 700px;

}
.videoRow > img{
    object-fit: contain;
    width: 246px;
    height: 138px;

}
.videoRow__text{
    margin-left: 14px;

}
.videoRow__subs{
    background-color: lightgray;
    padding: 2px;
}
.videoRow__description{
margin-top: 20px;
font-size: 12px;
color: #606060;
}
.videoRow__headline{
font-size: 12px;
color: #606060;
}
.videoRow__Numbers{
    font-weight: bolder;
    color: rgb(84, 157, 211);
}
